import $ from "jquery";
import "fullcalendar";

$(function() {
    var containerCal = $("#calendar");

	containerCal.fullCalendar({
		header: {
			center: "title"
		},
		navLinks: true,
		defaultView: "agendaWeek",
		allDaySlot: false,
    	weekend: false,
    	minTime: "07:00:00",
    	maxTime: "23:00:00",
    	slotEventOverlap: false,
    	slotDuration: "01:00:00",
    	nowIndicator: false,
    	events: []
	});

});